<template>
  <div>
    <scroller v-if="isPaging" :refreshText='refreshText' :noDataText='noDataText' :on-refresh="refresh" :on-infinite="loadMove" ref="my_scroller">
      <slot></slot>
    </scroller>
    <scroller :on-refresh="refresh" :refreshText='refreshText' :noDataText='noDataText' v-else ref="my_scroller">
      <slot></slot>
    </scroller>
  </div>
</template>
<script>
import scroller from './components/Scroller'
export default {
  name: "lbr-srcoll",
  data() {
    return {

    };
  },
  components:{scroller},
  props: {
    //
    isPaging: {
      type: Boolean,
      default: false
    },
    //
    refreshText: {
      type: String,
      default: ''
    },
    //
    noDataText: {
      type: String,
      default: ''
    },
    
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
      if(this.isPaging){
        this.$refs.my_scroller.finishInfinite(true); // 
      }
    },
    // 
    refresh() {
      this.$emit("refresh", () => {
       this.$refs.my_scroller.finishPullToRefresh();
      });
    },
    /**
     * 
     */
    loadMove() {
      this.$emit("loadMove", () => {
        this.$refs.my_scroller.finishInfinite(true); // 
      });
    },
    // 
    scrollTo(x,y,times){
      this.$refs.my_scroller.scrollTo(x,y,times)
    }
  }
};
</script>

<style lang="scss">
._v-container > ._v-content > .pull-to-refresh-layer {
  float: left;
}
</style>