
import Vue from 'vue'
import lbrMessage from './src/lbr-message'

function Message(param={}) {
  let msg;
  let duration = param.duration || 3000;
  if (typeof param.title === 'string') {
    msg = param.title
  } else if (param.title instanceof Object) {
    msg = param.title || ''
    if (param.duration) {
      duration = param.duration
    }
  }
  let VueMessage = Vue.extend({
    render(h) {
      let props = {
        text: msg,
        show: this.show
      }
      return h(lbrMessage, { props })
    },
    data() {
      return {
        show: false
      }
    }
  })
  let newMessage = new VueMessage()
  let vm = newMessage.$mount()
  let el = vm.$el
  document.body.appendChild(el) // dombody
  Vue.nextTick(()=>{
    vm.show = true
    let t1 = setTimeout(() => {
      clearTimeout(t1)
      vm.show = false  //300msbodydom
      let t2 = setTimeout(() => {
        clearTimeout(t2)
        document.body.removeChild(el) //bodydom
        newMessage.$destroy()
        vm = null // nulljs
        param.callBack && (typeof param.callBack === 'function') && param.callBack()
      }, 300)
    }, duration)
  })
 
  return newMessage
}

export default Message;
